#footer{
    background-color: var(--bg-dark);
}
#footer .nav-link{
    color: var(--footer-text-color);
    cursor: pointer;
}
#footer .nav-link:hover{
    text-decoration: underline;
}
.section-divide-footer{
    padding: 80px 40px!important;
}
.footerCopy{
    padding: 15px 10px;
    background-color: var(--deepDark);
    color: var(--footer-text-color);
    text-align: center;
}
.footerCopy p{
    margin: 0;
}