.topBannerImage{
    /*background-image: url("../../assets/images/topbanner_bg.jpg");*/
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 700px;
    width: 100%;
    position: relative;
}
.topBannerOverlay{
    position: absolute;
    min-height: 700px;
    width: 100%;
    background-color: var(--topBanner);
}
.topBannerBottom{
    width: 100%;
    padding: 50px 20px;
    position: relative;
}
.topBanner{
    padding-top: 100px;
}
