@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{

    --topBanner: rgba(5,97,82,0.9);
    --red: #F04E51;
    --red-hover: #EF4043;
    --green: rgba(5,97,82,1);
    --green-hover: #157347;
    --bg-dark: #252A3C;
    --deepDark: #1D2130;
    --white: white;
    --text-color: #707070;
    --body: #edeff0;
    --app-text-color: #1b2431;
    --footer-text-color: #80909A;
}


body{
    font-family: open sans, sans-serif;
    font-size: 15px;
    color: #707070;
    color: var(--text-color);
    background-color: #edeff0;
    background-color: var(--body);
    position: relative;
    line-height: 20px;
    font-width: 400;
}
.bg-success{
    background-color: rgba(5,97,82,1) !important;
    background-color: var(--green) !important;
}
.text-success{
    color: rgba(5,97,82,1) !important;
    color: var(--green) !important;
}
.background-dark{
    background-color: #252A3C;
    background-color: var( --bg-dark);
}
.text-bolder{
    font-weight: 700!important;
    color: #1b2431;
    color: var(--app-text-color)
}
.text-lighter{
    font-weight: 300!important;
}
.invert{
    color: #c5c5c5!important;
}



.btn{
    padding: 10px 25px;
    border-radius: 5px;
}
.btn-success{
    background-color: rgba(5,97,82,1);
    background-color: var(--green);
}
.btn-success:hover{
    background-color: #157347;
    background-color: var(--green-hover);
}

/*global margin*/
.marginLeft3{
    margin-left: 30px!important;
}
.marginBottom7{
    margin-bottom: 70px!important;
}
.marginBottom3{
    margin-bottom: 30px;
}

.app-invert{
    color: #c5c5c5;
}

.f-14{
    font-size: 14px !important;
}

.section-divide{
    padding: 80px 20px!important;
}




.app h1,.app h2, .app h3, .app h4, .app h5,.app h5{
    color: #1b2431;
    color: var(--app-text-color);
    font-weight: 300;

}
.app h1{
    font-size: 36px;
    line-height: 50px;
}
.app h2{
    font-size: 32px;
    line-height: 40px;
}
.app h3{
    font-size: 28px;
    line-height: 40px;
}
.app h4{
    font-size: 24px;
    line-height: 40px;
}
.app h5{
    font-size: 20px;
    line-height: 30px;
}
.app h6{
    font-size: 16px;
    line-height: 25px;
}

.aboveHeading{
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 13px;
    font-weight: 600;
}

.underHeading{
    float: left;
    width: 100%;
    color: #7f8fa4;
    font-size: 15px;
    line-height: 30px;
    margin-top: -10px;
    margin-bottom: 20px;
    font-weight: 300;
}

a{
    color: var(--white);
    cursor: pointer;
}
.topBannerImage{
    /*background-image: url("../../assets/images/topbanner_bg.jpg");*/
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 700px;
    width: 100%;
    position: relative;
}
.topBannerOverlay{
    position: absolute;
    min-height: 700px;
    width: 100%;
    background-color: var(--topBanner);
}
.topBannerBottom{
    width: 100%;
    padding: 50px 20px;
    position: relative;
}
.topBanner{
    padding-top: 100px;
}

.whyChoseWrapper{
    position: relative;
    background-color: var(--white);
}
whyChoseBottomWrapper2{
    background: var(--white);
}
.featureWrapper{
    position: relative;
}
.col-md-4{
    padding: 20px 10px;
}
.iconTopHeader{
    text-align: center;
    font-size: 40px;
    padding: 15px 10px;
}
.icon i{
    font-size: 30px;
}
#footer{
    background-color: var(--bg-dark);
}
#footer .nav-link{
    color: var(--footer-text-color);
    cursor: pointer;
}
#footer .nav-link:hover{
    text-decoration: underline;
}
.section-divide-footer{
    padding: 80px 40px!important;
}
.footerCopy{
    padding: 15px 10px;
    background-color: var(--deepDark);
    color: var(--footer-text-color);
    text-align: center;
}
.footerCopy p{
    margin: 0;
}
